
import XCircle from 'vux/src/components/x-circle/index.vue'
import Search from 'vux/src/components/search/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Scroll from './pulldown'

export default {
components: {
XCircle,
Search,
LoadMore,
Cell,
Group,
Badge,
Divider,
Tab,
TabItem,
Flexbox,
FlexboxItem,
'v-scroll': Scroll
},
data () {
return {
id: this.$route.params.id,
teamActivity: {},
percent: 0,
strokeWidth: 15,
trailWidth: 15,
activityList: [],
orderList: [],
index: 0,
cellTitle: '',
loadmore: false,
loadbottom: false,
carList: [],
loadingText: '',
pageNum: 1, // 当前页面
pageSize: 30,  // 一次显示多少条
downdata: []  // 上拉更多的数据存放数组
}
},
created: function () {

this.$vux.loading.show({
text: '加载中'
})
var params = 'id=' + this.$route.params.id
this.$http.post(this.domain + '/api/anon/car/getCarDetail', params).then((response) => {
this.$vux.loading.hide()
var data = response.data.data
this.teamActivity = data.teamActivity
var totalFinish = this.teamActivity.totalFinish
var targetAccount = this.teamActivity.target_account
this.percent = totalFinish / targetAccount * 100
this.activityList = data.activityList
this.orderList = data.orderList
})
},
watch: {

'$route': 'fetchData'
},
methods: {
fetchData: function (pageNum, pageSize) {
this.loadmore = true
var params = 'pageNum=' + pageNum + '&pageSize=' + pageSize
this.$http.post(this.domain + '/api/anon/car/getCarList', params).then((response) => {
this.carList = response.data.data
this.loadmore = false
})
},
onRefresh (done) {

this.loadbottom = false
this.carList = []
this.pageNum = 1
this.fetchData(1, 30)
done()
},
onInfinite (done) {
if (this.loadbottom === true) {
done()
return
} else {
this.loadmore = true
this.pageNum++
let pageNum = this.pageNum
var grade = this.getGrade(this.index)
var params = 'pageNum=' + pageNum + '&pageSize=' + this.pageSize + '&grade=' + grade
this.$http.post(this.domain + '/api/anon/activity/getActivityList', params).then((response) => {

this.downdata = response.data.data
this.carList = this.carList.concat(this.downdata)
if (this.downdata.length === 0) {
this.loadbottom = true
}
this.loadmore = false
done()
})
}
}
}
}



